import { createDirective, IRenderedDom,Module,Util as Util1 } from "nodom3";

//关闭右键菜单
document.oncontextmenu = function (e) {
    e.preventDefault();
};

/**
 * 工具类
 */
export class UITool {
    /**
     * 去掉字符串的空格
     * @param src 
     */
    static clearSpace(src: string): string {
        if (src && typeof src === 'string') {
            return src.replace(/\s+/g, '');
        }
    }

    /**
     * 计算实际位置
     * @param el    待计算的element
     * @returns     [x坐标,y坐标]
     */
    static getRelPos(el:HTMLElement):number[]{
        let x = el.offsetLeft - el.scrollLeft;
        let y = el.offsetTop - el.scrollTop;
        let offsetEl:HTMLElement = <HTMLElement>el.offsetParent;
        while(el){
            el = <HTMLElement>el.parentElement;
            x -= el.scrollLeft;
            y -= el.scrollTop;
            if(offsetEl === el || el === document.body){
                break;
            }
        }
        return [x,y];
    }

    /**
     * 获取实际位置
     * @param el    待计算的element
     * @returns     [x坐标,y坐标]
     */
    static getRealPos(el:HTMLElement){
        let x = el.offsetLeft - el.scrollLeft;
        let y = el.offsetTop - el.scrollTop;
        let offsetEl:HTMLElement = <HTMLElement>el.offsetParent;
        while(el){
            el = <HTMLElement>el.parentElement;
            x -= el.scrollLeft;
            y -= el.scrollTop;
            if(offsetEl === el){
                x += el.offsetLeft;
                y += el.offsetTop;
                offsetEl = <HTMLElement>offsetEl.offsetParent;
            }
            if(el === document.body){
                break;
            }
        }
        return [x,y];
    }

    /**
     * 计算位置
     * @param event         鼠标事件
     * @param relPos        相对位置：1下 2右
     * @param width         dom宽
     * @param height        dom高
     * @returns             [x,y]
     */
    static cacPosition(event:MouseEvent,relPos:number,width:number,height:number):number[]{
        const relEl = <HTMLElement>event.currentTarget;
        let w = relEl.offsetWidth;
        let h = relEl.offsetHeight;
        let el = <HTMLElement>event.target;
        //如果点击到内部元素，则通过向上计算偏移量
        let ox = 0;
        let oy = 0;
        
        //解决父对象transform不为none时的fixed定位问题
        let pox = 0;
        let poy = 0;
        let pel = relEl.parentElement;
        while(pel){
            let trans = window.getComputedStyle(<HTMLElement>pel,null).transform;
            if(trans !== 'none'){
                pox += (<HTMLElement>pel).offsetLeft;
                poy += (<HTMLElement>pel).offsetTop;
            }
            pel = pel.parentElement;
        }

        // 计算相对位置
        while(el && el !== relEl){
            if(el.offsetParent !== relEl && el.offsetParent.contains(relEl)){
                break;
            }
            ox += el.offsetLeft;
            oy += el.offsetTop;
            el = <HTMLElement>el.offsetParent;
        }
        
        //box 阴影面padding为5
        const padding=5;
        //页面高度和宽度
        const gwidth = window.innerWidth;
        const gheight = window.innerHeight;
        const px = event.clientX - event.offsetX - ox;
        const py = event.clientY - event.offsetY - oy;
        let x;
        let y ;
        if(relPos === 1){//纵向
            x = px - padding + 1;
            y = py + h;
            if(py + h + height > gheight + padding){  //下方不够放
                if(py + h > gheight/2 + padding){   //起始位置过半，空间较小，移动到上方
                    if(py < height+padding){ //不够放
                        height = py - padding * 2;
                    }
                    y = py-height;
                }else{
                    height = gheight - py - h - padding;
                    y = py + h;
                }
            }
        }else{ //横向
            x = px + w;
            y = py;
            if(x + width > gwidth + padding){  //右方不够放，放左放
                x = px - width;
            }
        }

        
        return [x-pox,y-poy,width,height];
    }

}




