
import {Module, registModule} from "nodom3";
import { BaseInput } from "./baseinput";

/**
 * radio group插件
 */
export class UIRadioGroup extends BaseInput{
    /**
     * 当前model
     */
    private __currentModel;
    /**
     * 子model数组
     */
    private __models = [];

    template(props){
        super.template(props);
        return `
            <div class='ui-radiogroup'>
                <slot />
            </div>
        `;
    }

    /**
     * 设置当前子model
     * @param model 
     */
    public __setCurrentModel(model){
        if(this.__currentModel){
            this.__currentModel.checked = false;
        }
        model.checked = true;
        this.__currentModel = model;
        super.__change(model.value);
    }


    /**
     * 添加子model
     * @param model     子model 
     * @returns 
     */
    public __addModel(model){
        if(this.__models.includes(model)){
            return;
        }
        this.__models.push(model);
        if(model.value == this.model['__value']){
            this.__setCurrentModel(model);
        }else{
            model.checked = false;
        }
    }

    /**
     * 更改值
     * @param value     新值
     */
    __change(value){
        super.__change(value);
        for(let m of this.__models){
            if(m['value'] == value){
                this.__setCurrentModel(m);
                break;
            }
        }
    }
}

/**
 * radio插件
 * 配置说明
 * field:           绑定父模块的字段
 */
export class UIRadio extends Module{
    template(props){
        this.model['value'] = props.value;
        return `
            <span class={{'ui-radio ' + (checked?'ui-radio-active':'ui-radio-unactive')}} e-click='__clickCheck'>
                <b/>
                <span>${props.title}</span>
            </span>    
        `
    }

    onBeforeFirstRender(model){
        const pm = <UIRadioGroup>this.getParent();
        pm.__addModel(model);
    }

    __clickCheck(model){
        (<UIRadioGroup>this.getParent()).__setCurrentModel(model);
    }
}

registModule(UIRadioGroup,'ui-radiogroup');
registModule(UIRadio,'ui-radio');