import { Module,$get,$set,watch } from "nodom3";

export class BaseInput extends Module{
    /**
     * 对应字段
     */
    protected __field:string;

    /**
     * change事件方法名
     */
    __onChangeName:string;

    /**
     * 禁用watch的触发器一次，避免插件修改值后出现重复initvalue操作
     */
    __disableTrigger:boolean;

    template(props){
        this.__field = props.field;
        this.__onChangeName = props.onChange;
        return null;
    }

    onBeforeFirstRender(){
        if(!this.__field){
            return;
        }
        watch(this.srcDom.model,this.__field,(m,k,ov,nv)=>{ 
            if(this.__disableTrigger){
                this.__disableTrigger = false;
                return;
            }
            this.__initValue();
        });
    }

    /**
     * 更改值
     * 需要对父模块对应数据项进行更改
     * @param value     新值
     */
    public __change(value:any){
        if(!this.__field){
            return;
        }
        let v1 = this.model['__value'];
        if(value===v1){
            return;
        }
        //调用change事件
        if(this.__onChangeName){
            this.getParent().invokeMethod(this.__onChangeName,v1,value);
        }
        this.model['__value'] = value;
        this.__disableTrigger = true;
        //更改父模块对应数据项
        $set(this.srcDom.model,this.__field,value);
    }

    /**
     * 每次刷新前获取新值
     */
    public onBeforeRender(){
        this.__initValue();
    }

    /**
     * 初始化value
     * @returns 
     */
    public __initValue(){
        if(!this.__field){
            return;
        }
        const v = $get(this.srcDom.model,this.__field);
        const v1 = this.model['__value'];
        if(v === v1){
            return;
        }
        this.model['__value'] = v;
    }

    /**
     * 获取值
     * @returns     模块值 
     */
    public __getValue(){
        return this.model['__value'];
    }
}